
<template>
	<div class="drag_box">
		<div class="drag_text" ref="drag_text">拖动滑块登录</div>
		<div class="drag_bg" ref="drag_bg">{{loginSuccess}}</div>
		<div class="drag_div" :class="dragSuccess?'drag_ok':''" ref="drag_div"></div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				dragSuccess: false, //成功判断
				beginClientX: 0, //触摸点距离屏幕左端距离
				loginSuccess: '', //成功文字
			}
		},
		computed:{
			slidingMaxWidth(){ //滑动最大宽度
				return this.$refs.drag_text.clientWidth - this.$refs.drag_div.clientWidth
			}
		},
		methods:{
			touchstart(e){ //触摸屏幕
				e.preventDefault();
				this.beginClientX = e.changedTouches[0].clientX; //触摸点距离屏幕左端距离
			},
			touchmove(e){ //滑动屏幕
				//console.log(e.changedTouches[0].clientX)
				//滑动点距离屏幕左端距离 - 触摸点距离屏幕左端距离 => 滑动的距离
				let slidingDistance = e.changedTouches[0].clientX - this.beginClientX;
				if(slidingDistance > 0 && slidingDistance <= this.slidingMaxWidth){ //最大滑动距离之间
					this.$refs.drag_div.style.left = slidingDistance + 'px';
					this.$refs.drag_bg.style.width = slidingDistance + 'px';
				}else if(slidingDistance < 0){ //滑动距离为负数(反向滑动)
					this.$refs.drag_div.style.left = 0 + 'px';
					this.$refs.drag_bg.style.width = 0 + 'px';
				}else if(slidingDistance > this.slidingMaxWidth){ //滑动距离 超出 最大滑动距离
					this.sildingSuccess();
				}
			},
			touchend(e){ //滑动/触摸 结束
				let slidingDistance = e.changedTouches[0].clientX - this.beginClientX;
				if(slidingDistance < this.slidingMaxWidth){
					this.$refs.drag_div.style.left = 0 + 'px';
					this.$refs.drag_bg.style.width = 0 + 'px';
				}
			},
			sildingSuccess(){ //成功
				this.dragSuccess = true;
				this.loginSuccess = '登录成功';
				this.$refs.drag_div.removeEventListener('touchmove',this.touchmove);
				this.$refs.drag_div.removeEventListener('touchend',this.touchend);
				this.$refs.drag_div.style.left = this.slidingMaxWidth + 'px';
				this.$refs.drag_bg.style.width = this.slidingMaxWidth + 'px';
				
				setTimeout(this.sendToParent,1000); //传参父组件
			},
			sendToParent(){ //传参父组件
				this.$emit('getLogin',true);
			}
		},
		mounted(){
			this.$refs.drag_div.addEventListener('touchstart',this.touchstart);
			this.$refs.drag_div.addEventListener('touchmove',this.touchmove);
			this.$refs.drag_div.addEventListener('touchend',this.touchend);
		}
	}
</script>

<style>
	.drag_box{
		position: relative;
		margin: 50% 5%;
	}
	.drag_text{
		line-height: 3rem;
    	background: #aaaaaa;
    	color: #fff;
	}
	.drag_bg{
		position: absolute;
		top: 0;
		left: 0;
		width: 0;
		height: 3rem;
		background: #7ac23c;
		line-height: 3rem;
		color: #fff;
	}
	.drag_div{
		height: 3rem;
    	width: 4rem;
	    background: url(data:image/png;base64,) no-repeat #fff 50%;
	    background-size: 50%;
	    position: absolute;
	    top: 0;
	    left: 0;
	    border: 1px solid #ccc;
    	box-sizing: border-box;
	}
	.drag_ok{
		background: url(data:image/png;base64,) no-repeat #fff 50%;
	    background-size: 50%;
	}
</style>



